import articleList from '@/components/articleList.vue';
const city = uni.getStorageSync('CITY');
export default {
	data() {
		return {
			type: -1,
			more: false,
			pageNum: 1,
			pageSize: 10,
			loading: true,
			list: []
		};
	},
	components: { articleList },
	onLoad(opt) {
		console.log('路由参数：', opt);
		if (opt.type) {
			this.type = opt.type;
			uni.setNavigationBarTitle({
				title: opt.title
			});
			this.getList();
		}
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getList() {
			if (this.loading) {
				this.loading = false;
				this.$Fetch({
					url: 'news/getData',
					data: {
						pageNum: this.pageNum,
						pageSize: this.pageSize,
						type: this.type,
						city: city
					},
					debug:true,
					success: res => {
						let arr=res.lists;
						if (arr.length > 0) {
							this.list = this.list.concat(arr);
							if (arr.length === this.pageSize) {
								this.pageNum++;
								this.loading = true;
								this.more = true;
							} else {
								this.more = false;
							}
						}
					}
				});
			}
		}
	}
};
